import React, { useState, useEffect } from 'react';
import { MapContainer, TileLayer, Marker, Popup, Polyline } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import { useAuth } from '../../contexts/AuthContext';

// Icône pour les bus (point bleu)
const busIcon = new L.DivIcon({
  html: '<div style="background-color: blue; width: 12px; height: 12px; border-radius: 50%; border: 2px solid white;"></div>',
  className: '',
  iconSize: [12, 12],
  iconAnchor: [6, 6],
});

// Icône pour les stations (point vert)
const stationIcon = new L.DivIcon({
  html: '<div style="background-color: green; width: 12px; height: 12px; border-radius: 50%; border: 2px solid white;"></div>',
  className: '',
  iconSize: [12, 12],
  iconAnchor: [6, 6],
});

const BusLocation = ({ bus = [] }) => {
  const { token } = useAuth();
  const [shapes, setShapes] = useState([]);
  const [stops, setStops] = useState([]);
  const [stopTimes, setStopTimes] = useState([]);
  const [trips, setTrips] = useState([]);
  const [selectedStation, setSelectedStation] = useState(null);
  const [timeToStation, setTimeToStation] = useState(null);
  const [busPositions, setBusPositions] = useState(bus);

  // Centre de la carte ajusté pour Monastir-Ouardanine
  const defaultCenter = [35.74, 10.75];
  const defaultZoom = 12;

  // Récupérer les données (trips, stops, stopTimes)
  useEffect(() => {
    const fetchData = async () => {
      if (!token) {
        console.error('Token manquant, impossible de récupérer les données');
        return;
      }

      try {
        const [tripsResponse, stopsResponse, stopTimesResponse] = await Promise.all([
          fetch('http://localhost:80/api/trips', { headers: { Authorization: `Bearer ${token}` } }),
          fetch('http://localhost:80/api/stops', { headers: { Authorization: `Bearer ${token}` } }),
          fetch('http://localhost:80/api/stoptimes', { headers: { Authorization: `Bearer ${token}` } }),
        ]);

        const [tripsData, stopsData, stopTimesData] = await Promise.all([
          tripsResponse.json(),
          stopsResponse.json(),
          stopTimesResponse.json(),
        ]);

        if (!tripsResponse.ok) throw new Error(tripsData.message || 'Erreur trips');
        if (!stopsResponse.ok) throw new Error(stopsData.message || 'Erreur stops');
        if (!stopTimesResponse.ok) throw new Error(stopTimesData.message || 'Erreur stopTimes');

        console.log('Trips data:', tripsData);
        console.log('Stops data:', stopsData);
        console.log('StopTimes data:', stopTimesData);

        setTrips(tripsData);
        setStops(stopsData);
        setStopTimes(stopTimesData);
      } catch (error) {
        console.error('Erreur fetch data:', error);
      }
    };

    fetchData();
  }, [token]);

  // Récupérer les shapes
  useEffect(() => {
    const fetchShapes = async () => {
      if (!token || trips.length === 0) return;

      try {
        const shapeIds = [...new Set(trips.map(trip => trip?.shape_id).filter(id => id))];
        console.log('Fetching shapes for shape_ids:', shapeIds);

        const shapePromises = shapeIds.map(async (shapeId) => {
          const response = await fetch(`http://localhost:80/api/shapes/byShapeId/${shapeId}`, {
            headers: { Authorization: `Bearer ${token}` },
          });
          const shapeData = await response.json();
          if (!response.ok) throw new Error(shapeData.message || `Erreur shape ${shapeId}`);
          return shapeData;
        });

        const shapesData = (await Promise.all(shapePromises)).filter(shape => shape?.points?.length > 0);
        console.log('All shapes data:', shapesData);

        const validShapes = shapesData.map(shape => ({
          ...shape,
          points: shape.points
            .sort((a, b) => (a.shape_pt_sequence || 0) - (b.shape_pt_sequence || 0))
            .map(point => [parseFloat(point.shape_pt_lat), parseFloat(point.shape_pt_lon)]),
        })).filter(shape => shape.points.length > 1);

        setShapes(validShapes);
      } catch (error) {
        console.error('Erreur fetch shapes:', error);
      }
    };

    fetchShapes();
  }, [trips, token]);

  // Mettre à jour les positions des bus
  useEffect(() => {
    const updateBusPositions = async () => {
      if (!token) return;

      try {
        const response = await fetch('http://localhost:80/api/vehicles', {
          headers: { Authorization: `Bearer ${token}` },
        });
        const vehiclesData = await response.json();
        if (!response.ok) throw new Error(vehiclesData.message || 'Erreur véhicules');

        setBusPositions(vehiclesData);
      } catch (error) {
        console.error('Erreur mise à jour bus:', error);
      }
    };

    const interval = setInterval(updateBusPositions, 10000);
    updateBusPositions();
    return () => clearInterval(interval);
  }, [token]);

  // Calcul de la distance (Haversine)
  const calculateDistance = (lat1, lon1, lat2, lon2) => {
    const R = 6371e3;
    const φ1 = (lat1 * Math.PI) / 180;
    const φ2 = (lat2 * Math.PI) / 180;
    const Δφ = ((lat2 - lat1) * Math.PI) / 180;
    const Δλ = ((lon2 - lon1) * Math.PI) / 180;

    const a = Math.sin(Δφ / 2) * Math.sin(Δφ / 2) +
      Math.cos(φ1) * Math.cos(φ2) * Math.sin(Δλ / 2) * Math.sin(Δλ / 2);
    const c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));

    return R * c;
  };

  // Calcul du temps restant
  const calculateTimeToStation = (bus, station) => {
    if (!bus?.latitude || !bus?.longitude || !station?.stop_lat || !station?.stop_lon) return null;

    const distance = calculateDistance(
      parseFloat(bus.latitude),
      parseFloat(bus.longitude),
      parseFloat(station.stop_lat),
      parseFloat(station.stop_lon)
    );

    const speed = 40 / 3.6; // 40 km/h en m/s
    const timeInSeconds = distance / speed;
    return Math.round(timeInSeconds / 60);
  };

  // Trouver la station suivante
  const findNextStation = (bus, trip) => {
    if (!bus?.latitude || !bus?.longitude || !trip?.stop_times?.length) return null;

    const stopTimesWithStops = stopTimes
      .filter(st => trip.stop_times.includes(st._id.toString()))
      .map(st => ({
        stopTime: st,
        stop: stops.find(s => s._id.toString() === st.stop.toString()),
      }))
      .filter(st => st.stop)
      .sort((a, b) => a.stopTime.stop_sequence - b.stopTime.stop_sequence);

    let closestStop = null;
    let minDistance = Infinity;

    stopTimesWithStops.forEach(({ stop }) => {
      const distance = calculateDistance(
        parseFloat(bus.latitude),
        parseFloat(bus.longitude),
        parseFloat(stop.stop_lat),
        parseFloat(stop.stop_lon)
      );
      if (distance < minDistance) {
        minDistance = distance;
        closestStop = stop;
      }
    });

    const currentIndex = stopTimesWithStops.findIndex(st => st.stop._id.toString() === closestStop._id.toString());
    return currentIndex + 1 < stopTimesWithStops.length ? stopTimesWithStops[currentIndex + 1].stop : null;
  };

  // Gestion du clic sur une station
  const handleStationClick = (stop) => {
    setSelectedStation(stop);

    console.log('Station cliquée:', stop.stop_name);
    console.log('Tous les trips:', trips);
    console.log('StopTimes:', stopTimes);

    const trip = trips.find(t =>
      t.vehicle_id &&
      stopTimes.some(st => st.stop.toString() === stop._id.toString() && t.stop_times.includes(st._id.toString()))
    );

    console.log('Trip trouvé:', trip);

    if (!trip) {
      console.log('Aucun trip trouvé pour cette station');
      setTimeToStation(null);
      return;
    }

    const busItem = busPositions.find(b => b._id.toString() === trip.vehicle_id.toString());
    console.log('Bus trouvé:', busItem);

    if (!busItem) {
      console.log('Aucun bus trouvé pour le vehicle_id:', trip.vehicle_id);
      setTimeToStation(null);
      return;
    }

    const time = calculateTimeToStation(busItem, stop);
    setTimeToStation(time);

    const nextStation = findNextStation(busItem, trip);
    if (nextStation && nextStation._id.toString() === stop._id.toString()) {
      console.log('Station cliquée est la prochaine station');
    }
  };

  return (
    <div className="h-full w-full relative">
      <MapContainer center={defaultCenter} zoom={defaultZoom} style={{ height: '100%', width: '100%', minHeight: '600px' }}>
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='© <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />

        {/* Afficher les bus (points bleus) */}
        {busPositions
          .filter(b => b.latitude && b.longitude)
          .map(b => (
            <Marker
              key={b._id}
              position={[parseFloat(b.latitude), parseFloat(b.longitude)]}
              icon={busIcon}
            >
              <Popup>
                Bus: {b.uniqueId || 'Ligne Monastir-Ouardanine'} <br />
                Itinéraire: Monastir - Ouardanine
              </Popup>
            </Marker>
          ))}

        {/* Afficher les itinéraires (shapes) en rouge */}
        {shapes.length > 0 ? (
          shapes.map((shape, index) => (
            <Polyline
              key={index}
              positions={shape.points}
              color="red"
              weight={4}
              opacity={0.8}
            />
          ))
        ) : (
          <div className="absolute top-2 left-2 bg-yellow-100 text-yellow-800 p-2 rounded">
            Aucun trajet disponible. Vérifiez les données des shapes.
          </div>
        )}

        {/* Afficher les stations (points verts) */}
        {stops
          .filter(stop => stop.stop_lat && stop.stop_lon)
          .map(stop => (
            <Marker
              key={stop._id}
              position={[parseFloat(stop.stop_lat), parseFloat(stop.stop_lon)]}
              icon={stationIcon}
              eventHandlers={{ click: () => handleStationClick(stop) }}
            >
              <Popup>
                Station: {stop.stop_name} <br />
                {selectedStation?._id === stop._id && timeToStation !== null ? (
                  <span>Temps restant: {timeToStation} minutes</span>
                ) : (
                  <span>Cliquez pour voir le temps restant</span>
                )}
              </Popup>
            </Marker>
          ))}
      </MapContainer>

      {/* Afficher les détails de la station sélectionnée */}
      {selectedStation && (
        <div className="p-4 bg-white shadow-md rounded-lg mt-4">
          <h3 className="text-lg font-semibold">Station sélectionnée : {selectedStation.stop_name}</h3>
          {timeToStation !== null ? (
            <p>Temps estimé pour l'arrivée du bus : {timeToStation} minutes</p>
          ) : (
            <p>Aucun bus associé ou données insuffisantes.</p>
          )}
        </div>
      )}
    </div>
  );
};

export default BusLocation;